import React from "react";
import { hooks } from "../../hooks";
import { errorAlert, successAlert } from "../../utils";
import { ToastContainer, toast } from "react-toastify";

import { Button, FormGroup, PasswordInput } from "../../containers";
import { useIsMutating } from "@tanstack/react-query";
import { Link, useNavigate, useParams } from "react-router-dom";
import { useState } from "react";
import { useContext } from "react";
import { AuthContext } from "../../context";
import { useEffect } from "react";
import PageLayout from "../../layout";

const ResetPassword = () => {
  const { id } = useParams();
  const isLoading = useIsMutating();
  const [formData, setFormData] = useState({});
  const [errors, setErrors] = useState({});
  const navigate = useNavigate();
  const handleChange = (name, value) => {
    setFormData({ ...formData, [name]: value });
  };
  const validationHandler = (name, error) => {
    setErrors({ ...errors, [name]: error });
  };

  const { mutate, isSuccess, isError, error, reset } = hooks.useResetPassword();
  if (isError) {
    reset();
    errorAlert(error);
  }
  if (isSuccess) {
    reset();
    successAlert("Password Reset Successful");
    setFormData("");
    navigate("/login");
  }
  const submitHandler = () => {
    if (formData["password"] !== formData["confirmPassword"]) {
      return toast.error("Password Does not Match");
    }
    mutate({ ...formData, token: id });
  };

  const { user } = useContext(AuthContext);

  useEffect(() => {
    if (user) {
      navigate("/");
    }
  }, [user, navigate]);

  return (
    <PageLayout>
      <div className="userLogin">
        <div className="userLoginCont">
          <div>
            <div className="formPadding">
              <h2>Reset Password?</h2>
              <br />
              <p>Please Enter your New Password</p>
              <br />
              {/* <p>Please Enter your Email</p> */}
              <br />
            </div>
            <ToastContainer />
            <div>
              <FormGroup
                onSubmit={submitHandler}
                validation={formData}
                errors={errors}
                setErrors={setErrors}
              >
                <PasswordInput
                  name="password"
                  label="Password"
                  value={formData["password"]}
                  onChange={handleChange}
                  type="password"
                  validationHandler={validationHandler}
                  error={errors.password}
                  required={true}
                  size="large"
                />
                <PasswordInput
                  name="confirmPassword"
                  label="Confirm Password"
                  value={formData["confirmPassword"]}
                  onChange={handleChange}
                  type="password"
                  validationHandler={validationHandler}
                  error={errors.confirmPassword}
                  required={true}
                  size="large"
                />

                <Button
                  title="Reset Password"
                  loading={isLoading === 1}
                  disabled={isLoading === 1}
                  size="large"
                  className="btnSuccess"
                />

                {/* <div className="formButtonContainer center">
                  <span>
                    Don't have an account?{" "}
                    <Link to="/create-account">Create One</Link>
                  </span>
                </div> */}
              </FormGroup>
            </div>
          </div>
        </div>
      </div>
    </PageLayout>
  );
};

export default ResetPassword;
